<template lang="pug">

.history(:class="{active: active, fixed: fixed}" :style="old")
	.fade(@click="hide")
	.history-card(:style="{height: now.height, top: now.top, left: now.left}")
		.history-inner
			.history-head
				.history-date {{data.date}}
				.history-sum {{data.sum}} р / {{data.cart.length}} наим
			.history-info
				.history-info-item
					span Адрес доставки:
					span {{data.address}}
			.history-cart
				.history-cart-card(v-for="card in data.cart")
					.history-cart-card-name {{card.name}}
					.history-cart-card-price {{card.price}}
					.history-cart-card-sum {{card.sum}}
			.history-more(@click="show") Подробнее

</template>

<script>

export default {
	mounted(){
		this.old.height = this.inner.offsetHeight + 'px',
		this.now.height = this.old.height
		this.resize()
	},
	data () {
		return {
			active: 0,
			fixed: 0,
			old: {},
			now: {
				top: 0,
				left: 0,
				height: 'auto'
			}
		}
	},
	methods: {
		show(){
			this.resize()
			this.fixed = 1
			this.active = 1
			setTimeout(() => {
				this.now.height = this.inner.offsetHeight + 'px'
			}, 30);
		},
		hide(){
			this.resize()
			this.active = 0
			this.now.height = this.old.height
			setTimeout(() => {
				this.fixed = 0
			}, 300);
		},
		resize(){
			var pos = this.$el.getBoundingClientRect();
			this.now.top = pos.top + 'px'
			this.now.left = pos.left + 'px'
		}
	},
	computed: {
		inner(){
			return this.$el.querySelector('.history-inner')
		}
	},
	props: ['data']
}

</script>

<style lang="stylus" scoped>

.history
	position relative
	margin-right 22px
	width 345px
	&:nth-child(3n)
		margin-right 0
	.fade
		position absolute
		width 0
		height 0
		z-index 1
		left 0
		top 0
		background-color rgba(#000, .3)
		opacity 0
		transition opacity .3s
	&-card
		margin-bottom 20px
		width 334px
		background-color #fff
		box-shadow 0 0 6px 0 rgba(#000, .1)
		border-radius: 15px
		overflow hidden
	&-inner
		position relative
		width 100%
		padding 15px 20px 20px
	&.fixed
		.history-card
			z-index 201
			position fixed
			transition all .3s ease-in-out
		.fade
			width 100%
			height 100%
			left 0
			top 0
			cursor pointer
			position fixed
			z-index 200
	&.active
		.fade
			opacity 1
		.history
			&-card
				width 550px!important
				left 50%!important
				top 50%!important
				left 50vw!important
				top 50vh!important
				transform translate(-50%, -50%)
			&-more
				display none
			&-cart
				display block
				margin-bottom -5px
			&-inner
				width 550px
	&-head
		display flex
		justify-content space-between
		margin-bottom 10px
	&-date
		font-size 13px
	&-sum
		font-size 13px
	&-more
		font-size 16px
		color #0f93ff
		cursor pointer
		margin-top 5px
		margin-bottom -5px
	&-cart
		margin-top 15px
		display none
		&-card
			border-radius 10px
			padding 12px 15px
			margin-bottom 12px
			box-shadow 0 0 3px 0 rgba(#000, .2)
			display flex
			justify-content space-between
			&-name, &-maker, &-price, &-sum
				font-size 14px
			&-sum
				font-weight 600
			&:last-child
				margin-bottom 0
	&-info
		display flex
		justify-content space-between
		flex-wrap wrap
		&-item
			margin-bottom 3px
			span
				font-size 13px
				&:first-child
					margin-right 7px
					color #999

</style>